<template>
    <div id="wangeditor">
        <div class="wangeditor" ref="editorElement" style="text-align:left"></div>
    </div>  
</template>

<script>
//  node node  http://ueditor.baidu.com/website/
import E from 'wangeditor'
export default {
    name: '',
    data () {
      return {
        editorContent: '',
      }
    },
    props:['ueData'], // 
    mounted() {
        var editor = new E(this.$refs.editorElement) //        
        editor.customConfig.onchange = (html) => {
            this.editorContent = html
            this.ueData(html)  //htmlueData
        }
        editor.customConfig.uploadImgServer = 'http://localhost:3000/user/ueditor' // 
        editor.customConfig.uploadFileName = 'img' // 
        editor.customConfig.uploadImgHeaders = {
            'Accept': '*/*',
            // 'Authorization':'Bearer ' + token    //token
        }
        editor.customConfig.menus = [          //
            'head',
            'list',  // 
            'justify',  // 
            'bold',
            'fontSize',  // 
            'italic',
            'underline',
            'image',  // 
            'foreColor',  // 
            'undo',  // 
            'redo',  // 
          ] 
        //
        editor.customConfig.uploadImgHooks = {
            before: function (xhr, editor, files) {
                // 
                // xhr  XMLHttpRequst editor files 
                
                //  {prevent: true, msg: 'xxxx'} 
                // return {
                //     prevent: true,
                //     msg: ''
                // }
            },
            success: function (xhr, editor, result) {
                // 
                // xhr  XMLHttpRequst editor result 
                this.imgUrl=Object.values(result.data).toString()
            },
            fail: function (xhr, editor, result) {
                // 
                // xhr  XMLHttpRequst editor result 
            },
            error: function (xhr, editor) {
                // 
                // xhr  XMLHttpRequst editor 
            },
            timeout: function (xhr, editor) {
              // 
                // xhr  XMLHttpRequst editor 
            },

            //  {errno:0, data: [...]} 
            //  JSON 
            customInsert: function (insertImg, result, editor) {
                // 
                // insertImg editor result 
                //  {url:'....'} 
                let url = Object.values(result.data)      //result.data
                JSON.stringify(url)    //JSON
                insertImg(url)
                // result  JSON 
            }
        }
        editor.create()  
    }
 }
</script>

<style lang="scss" scoped>
.wangeditor {
  background-color: #fff;
}
</style>



